@import './responsive.scss';

button {
  cursor: pointer;
}

.App {
  background-color: #000;
  color: white;
  cursor: default;
  
  ul {
    list-style-type: none;
  }
}

.modalOut {
  position: fixed;
  top:0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 9998;
  background-color: rgba(0, 0, 0, 0.5);
}

.closeBtn {
  position: absolute;
  top: 1px;
  right: 1px;
  background-color: rgba(0, 0, 0, 0);
  color: #fff;
  border: none;
}

header { // 시멘틱태그 공부
  width: 100vw;
  position: fixed;
  background-color: rgba(0, 0, 0, 0);
  z-index: 8888;
  
  .logo {
    position: absolute;
    left: 6.25vw;;
    top: 20px;
    background-color: rgba(0, 0, 0, 0);
    border: none;
  }
  
  nav{  // ul, li를 써보자!
    
    button {
      position: absolute;
      top: 20px;
      background-color:rgba(0, 0, 0, 0);
      border: none;
      color: white;
      
    }
    .search {
      right: 15vw;
      .searchIcon {
        width: 30px;
      }
    }
    
    .menu {
      right: 6.25vw;
      .menuIcon {
        width: 30px;
      }
    }
  }
  

  .searchModal{
    position: absolute;
    right: 15vw;
    top: 20px;
    display: flex;
    flex-direction: column;
    width: 300px;
    border-radius: 16px;
    background-color: #000;
    z-index: 9999;
    
    .searchInput {
      position: relative;
      width: 300px;
      height: 35px;
      border-bottom: 1px solid white;
      
      .searchText{
        width: 100%;
        height: 100%;;
        background-color: rgba(0, 0, 0, 0);
        color: #fff;
        border: none;
        outline: none;
      }

      .searchBtn {
        position: absolute;
        top : 55%;
        right: 0;
        transform: translate(0, -50%);
        background-color: rgba(0, 0, 0, 0);
        border: none;
        .searchBtnIcon {
          width: 30px
        }
      }

    }
    .words {
      .wordsBtns {
        display: flex;
        flex-direction: row;
        width: 300px;
        border-bottom: 1px solid white;
        button {
          width: 150px;
          margin: 10px;
          background-color: rgba(0, 0, 0, 0);
          font-size: medium;
          color: #fff;
        }

        .selectedBtn {
          background-color: #fff;
          color: #000;
        }
      }
    }

    .wordList {
      text-align: center;
      color: #fff;
      
        .word {
          margin: 10px 0;

          button{
            position: absolute;
            background-color: rgba(0, 0, 0, 0);
            border: none;
            color: #fff;
          }
        }

      

      
    }
  }

  .menuModal {
    background-color: #404040;
    width: 120px;
    position: absolute;
    right: 3vw;
    top: 40px;
    padding: 0;
    z-index: 9997;

    button {
      width: 100%;
      padding: 10px 0;
      margin-top: -1px;
      border: 1px solid black;
      border-collapse: collapse;
      text-align: center;
      background-color: rgba(0, 0, 0, 0);
      color: #fff;
    }
  }

  .loginModal {
    width: 450px;
    height: 320px;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 10vh auto;
    border: 1px solid black;
    background-color: #404040;
    color: #fff;
    
    z-index: 9999;

    .loginTitle {
      margin-bottom: 5px;
    }

    .warnning {
      height: 10px;
      font-size: 10px;
      color: red;
    }

    .hidedWarnning {
      height: 10px;
    }

    .loginInput {
      width: 70%;
      height: 40px;
      background-color: rgba(0, 0, 0, 0);
      color: #fff;
      outline: none;
      border: none;
      border-bottom: 1px solid black;
      margin-bottom: 30px;
    }

    .saveId{
      width: 71%;
      transform: translateY(-15px);
      font-size: 13px;
    }

    .loginBtn {
      width: 100px;
      height: 40px;
      border: none;
      background-color: #fff;
    }

    .loginMenuBtns {
      margin-top: 10px;
      button {
        border: none;
        color:#fff;
        background-color: rgba(0, 0, 0, 0);
      }
    }
  }
}

footer {
  padding: 120px 0;
  text-align: center;
  background-color: #000;

  div {
    margin: 10px;
  }

  span {
    margin: 10px
  }
}


main {
  width: 100%;
  overflow: hidden;
  

  .home{
    @include desktop{
      .hero {
        width: 100vw;
        height: 40vw;
  
        margin-bottom: 65px;
      }
    }
    @include tablet{
      .hero {
        width: 100vw;
        height: 39.0625vw;
  
        margin-bottom: 10px;
      }
    }
    @include mobile{
      .hero {
        width: 100vw;
        height: 97.2vw;

        margin-bottom: 14px;
      }
    }
    .movieLists {
      margin-top: 0;
    
      .movieList {
        width: 100vw;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        position: relative;
        @include desktop{
          margin-bottom: 65px;
          transform: translateX(-80px);
          
          .voidReducedTag {
            width: 100%;
            height: 35px;
          }

          .reducedTag {
            width: 100%;
            height: 35px;
            margin-left: 10px;
            text-align: start;
            
            .tagIcon {
              width: 25px;
            }
            
            .tagName {
              position: relative;
              bottom: 5px;
            }
      
          }
        }
        @include tablet{
          margin-bottom: 10px;
          transform: translateX(-80px);
          
          .voidReducedTag {
            width: 100%;
            height: 35px;
          }

          .reducedTag {
            width: 100%;
            height: 35px;
            margin-left: 10px;
            text-align: start;
            
            .tagIcon {
              width: 25px;
            }
            
            .tagName {
              position: relative;
              bottom: 5px;
            }
      
          }
        }

        @include mobile{
          margin-bottom: 10px;
          transform: translateX(-80px);
          
          .voidReducedTag {
            width: 100%;
            height: 10px;
          }

          .reducedTag {
            width: 100%;
            height: 10px;
            margin-left: 10px;
            text-align: start;
            
            .tagIcon {
              transform: translateY(-13px);
              margin-right: 3px;
              width: 10px;
            }
            
            .tagName {
              position: relative;
              bottom: 15px;
              font-size: xx-small;
            }
      
          }
        }

      
        .movieScroll {
          overflow-x: scroll;
          display: flex;
          flex-direction: row;
          width: 100%;
          height: 100%;
          
          .tag {
            @include desktop {
              padding-right: 50px;
              height: 163px;
              width: 80px;
            }
            @include tablet {
              padding-right: 50px;
              height: 124px;
              width: 80px;
            }
            @include mobile {
              padding-right: 10px;
              height: 60px;
              width: 80px;
            }
            display: flex;
            flex-direction: column;
            justify-content: center;
            text-align: center;
            
            .tagIcon {
              @include desktop {
                width: 80px;
              }
              @include tablet {
                width: 50px;
              }
              @include mobile {
                width: 30px;
              }
            }
            .tagName {
              @include desktop {
                width: 80px;
              }
              @include tablet {
                width: 80px;
              }
              @include mobile {
                width: 50px;
                font-size: x-small;
              }
            }
          }

          .scrollBtn{
            position: absolute;
            @include desktop {
              height: 163px;
              font-size: x-large;
            }
            @include tablet {
              height: 124px;
              font-size: x-large;
            }
            @include mobile {
              height: 60px;
              font-size: small;
            }
            color: #fff;
            background-color: rgba(0, 0, 0, 0.3);
            border: none;
            z-index: 7777;
          }
            
          .prevBtn {
            @extend .scrollBtn;
            left: 40px;
          }
            
          .nextBtn {
            @extend .scrollBtn;
            right: 18px;
          }
          
          .movie{
            position: relative;
            @include desktop {
              margin: 0 5px;
              .moviePoster { // ul, li로 묶기
                width: 290px;
                height: 163px;
              }
            }
            @include tablet {
              margin: 0 4px;
              .moviePoster { // ul, li로 묶기
                width: 228px;
                height: 124px;
              }
            }
            @include mobile {
              margin: 0 4px;
              .moviePoster { // ul, li로 묶기
                width: 107px;
                height: 60px;
              }
            }
            &:hover {
              .layer {
                display: block;
              }
              .movieInfo{
              display: flex;
              }
            }
            .layer {
              display: none;
              position: absolute;
              width: 100%;
              height: 100%;
              background: linear-gradient(
                to bottom,
                rgba(0, 0, 0, 0) 20%,
                rgba(0, 0, 0, 0.7) 100%
              );
            }
          
          .movieInfo {
            display: none;

            .title {
              position: absolute;
              bottom: 15px;
              left: 15px;
            }

            .movieMenu {
              position: absolute;
              bottom: 15px;
              right: 10px;
              display: flex;
              flex-direction: row;
              button {
                background-color: rgba(0, 0, 0, 0);
                color: white;
                border: none;
              }
            }
          }
        }
      }
      
      ::-webkit-scrollbar {
        display: none;
      }

    }
  }

    .detailModal { 
      background-color: #000;
      position: fixed;
      top: 5%;
      left: 50%;
      transform: translate(-50%, 0);
      width: 600px;
      height: 800px;
      z-index: 9999;
       
      .detailImg {
        width: 100%;
       }

      .playBtn {
        position: absolute;
        top: 38.5%;
        right: 92%;
        background-color: rgba(0, 0, 0, 0);
        color: #fff;
        border: none;
      }
      
      .keepBtn {
        position: absolute;
        top: 38.5%;
        left: 94%;
        background-color: rgba(0, 0, 0, 0);
        color: #fff;
        border: none;
      }
      
       .detailInfo {
        width: 100%;
        .runningTime {
          width: 98%;
          text-align: end;
          font-size: 12px;
        }
        .title {
          margin-left: 37px;  
        }
        .summary {
          margin: 0 37px;
          font-size: 15px;
        }
        .otherInfo {
          margin-top: 20px;
          font-size: 15px;
        }
        
       }
    }
  
    .gradiant {
      position: fixed;
      bottom: 0;
      width: 100vw;
      height: 120px;
      z-index: 6666;
      background: linear-gradient(
        to bottom,
        rgba(0, 0, 0, 0) 0%,
        rgba(0, 0, 0, 1) 100%
      );
    }
  }


  .player {

    .playerLayer {
      background-color: rgba(0, 0, 0, 0);
      position: absolute;
      top: 0;
      width: 100vw;
      height: 100vh;
      z-index: 7777;
    }
    
    .videoContainer {
      .video {
        
      }
    
      .controls {

        
        // input[type=range] {
        //   width:100%;
        //   // -webkit-appearance: none;
        //   height: 1px;
        //   outline: none;
        //   background-color: #fff;
        //   ::-moz-range-thumb {
        //     color: #000;
        //   }
        // }
        
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        position: fixed;
        bottom: 5px;
        
        width: 100vw;
        height: 40px;
        z-index: 8888;

        .backBtn {
          position: fixed;
          top: 10px;
          left: 10px;
          background-color: rgba(0, 0, 0, 0);
          border: none;
        }
        
        .reportBtn {
          position: fixed;
          top: 10px;
          right: 10px;
          background-color: rgba(0, 0, 0, 0);
          border: none;
        }

        .reportModal {
          position: fixed;
          top: 10px;
          right: 10px;
          background-color: #fff;
          color:#000;
          z-index: 9999;
          display: flex;
          flex-direction: column;
          border: none;
          
          .reportComment {
            border: 1px solid black;
          }

          button {
            margin-top: -1px;
            border: 1px solid black;
            padding: 10px 0;
            border-collapse: collapse;
            text-align: center;
            background-color: rgba(0, 0, 0, 0);
            color: #000;
          }
        }

        .centerPlayPauseBtn {
          position: fixed;
          top: 50%;
          left: 50%;
          background-color: rgba(0, 0, 0, 0.3);
          width: 35px;
          height: 35px;
          border-radius: 50%;
          border: none;
        
          .centerPlayPauseBtnIcon {
            transform: translate(-11%, 8%);
          }
        }

        .leftControls{
          display: flex;
          flex-direction: row;
          margin-top: 10px;

          .playPauseControl {
            .playPauseBtn {
              background-color: rgba(0, 0, 0, 0);
            }
          }

          .backwardForwardControl {
            display: flex;
            flex-direction: row;

            button {
              background-color: rgba(0, 0, 0, 0);
            }
          }
  
          .muteControl {
            .muteBtn {
              background-color: rgba(0, 0, 0, 0);
            }
          }
    
          .volumeControl{  
            .volumeSlider {
              width: 50px;
              margin-top: 7px;
            }
          }
        }

        .middleControls{
          margin-top: 15px;

          .timeControl {
            display: flex;
            flex-direction: row;
            .timeSlider {

              @include desktop {
                width: 70vw;
              }
              @include tablet {
                width: 40vw;
              }
            }
            .timeInfo {
              margin: 0 20px;
            }
          }
        }
         
        .rightControls {
          display: flex;
          flex-direction: row;
          margin-top: 12px;

          .playbackRateControl {
            width: 70px;
  
            .playbackRateControlBtn {
              color: #fff;
              border: none;
              background-color: rgba(0, 0, 0, 0);
            }
    
            .playbackRateModal {
              display: flex;
              flex-direction: column;
              position: fixed;
              bottom: 40px;
              background-color: rgba(0, 0, 0, 0.5);
              width: 70px;
        
              button {
                background-color: rgba(0, 0, 0, 0);
                border: none;
                color: #fff;
                width: 70px;
                height: 25px;

                .playbackRateCheck {
                  position: absolute;
                  left: 2px;
                }
              }
            }
          }

          .fullScreenControl {
            margin-top: 2px;
            button {
              background-color: rgba(0, 0, 0, 0);
              border: none;
              color: #fff;
            }
          }
        }
      }

    }
    
    
  }

  .search {
    width: 100vw;
    height: 100%;

    .searchWord {
      text-align: center;
      margin-top: 80px;
    }

    .comment {
      height: 20vh;
      margin-top: 150px;
      text-align: center;
    }

    .searchedMovieList {
      margin: 100px 0;
      .searchedMovie {
        display: flex;
        flex-direction: row;
        cursor: pointer;
        @include desktop {
          margin: 30px 20vw;
        }
        @include tablet {
          margin: 30px 10vw;
        }

        .searchedMovieImg {
          margin-right: 20px;
          .moviePoster {
          }
        }
        
        .searchedMovieInfo {
          display: flex;
          flex-direction: column;
          
          .movieTitle {
            font-size: larger;
          }
          .summary {
            margin-top: 10px;
          }
        }
      }
    }
  }

}

//시멘틱 태그, 가운데정렬 분산, ul li 
// top50%, 50% 위로 